/**
 * @link https://www.tinymce.com/css/codepen.min.css
 */
.mce-container textarea {
  display: initial !important; }

.mce-content-body {
  font-size: 16px;
  color: #828282;
  padding: 0 25px 25px; }

.mce-content-body a {
  color: #007bff; }

.mce-content-body h1 {
  font-size: 34px; }

.mce-content-body h2 {
  font-size: 30px; }

.mce-content-body h3 {
  font-size: 26px; }

.mce-content-body h4 {
  font-size: 22px; }

.mce-content-body h5 {
  font-size: 18px; }

.mce-content-body h6 {
  font-size: 14px; }

.mce-content-body p {
  margin: 25px 0; }

.mce-content-body ol,
.mce-content-body ul {
  padding-left: 15px;
  list-style-position: outside;
  margin-bottom: 20px; }

.mce-content-body ol li,
.mce-content-body ul li {
  margin-left: 10px;
  margin-bottom: 10px;
  color: #828282; }

.mce-content-body ul {
  list-style-type: disc; }

.mce-content-body ol {
  list-style-type: decimal; }

.mce-content-body table {
  width: 100%;
  border-spacing: 0;
  border-collapse: separate;
  border: 1px solid #aaa; }

.mce-content-body table tr:nth-child(even) {
  background: #fafafa; }

.mce-content-body table caption,
.mce-content-body table td,
.mce-content-body table th {
  padding: 15px 7px;
  font: inherit; }

.mce-content-body table th {
  font-weight: 400;
  color: #6e6e6e;
  background-position: 100% 100%;
  background-size: 2px 10px;
  background-repeat: no-repeat; }

.mce-content-body table th:last-child {
  background: 0 0; }

/* Luna Hack */
.mce-content-body hr#luna-readmore {
  background: #eee;
  content: "READMORE";
  height: 20px;
  display: block;
  text-align: center; }

.mce-content-body hr#luna-readmore::before {
  content: "READMORE";
  color: #999; }

.mce-content-body img {
  max-width: 100%;
  height: auto !important; }
